import './App.css';
import NavBar from './NavBar.js';
import Elsewhere from './Elsewhere.js';
import About from './pages/About.js';
import Teaching from './pages/Teaching.js'
import Work from './pages/Work.js';
import React, { useState, useEffect } from "react"

function App() {
  const [isMobile, setIsMobile] = useState(window.innerWidth <= window.innerHeight);

  useEffect(() => {
    const handleResize = () => {
      setIsMobile(window.innerWidth <= window.innerHeight);
    };

    window.addEventListener("resize", handleResize);

    return () => {
      window.removeEventListener("resize", handleResize);
    };
  }, []);


    if (isMobile) {
      return ( <div> mobile site currently under construction!</div>)
    } else {
      return (
        <div className="App">
          <NavBar/>
          <Elsewhere/>
          <About/>
          <div id='teaching1'> <Teaching/> </div>
          <div id='work1'> <Work/> </div>
        </div>
      );
    }
}

export default App;
